<template>
	<view class="container">
		<view class="register">
			<view class="info_box">
				<view class="info_item">
					<text class="label">客户姓名</text>
					<u-input v-model="form.custName" type="text" placeholder="请填写客户姓名" />
				</view>
				<view class="info_item">
					<text class="label">手机号码</text>
					<u-input v-model="form.custPhone" type="text" placeholder="请填写手机号" maxlength="11" />
				</view>
				<view class="info_item">
					<text class="label">性别</text>
					<picker @change="sexChange" :value="form.gender" :range="sexList">
						<view class="flexBet">
							<input type="text" disabled="true" placeholder="请选择性别" :value="sexList[form.gender]"
								placeholder-class="phcolor" />
							<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
						</view>
					</picker>
				</view>
				<view class="info_item">
					<text class="label">客户来源</text>
					<picker @change="customChange" :value="form.custSource" :range="customList">
						<view class="flexBet">
							<input type="text" disabled="true" placeholder="请选择客户来源"
								:value="customList[form.custSource]" placeholder-class="phcolor" />
							<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
						</view>
					</picker>
				</view>
				<view class="info_item" v-if="form.custSource==1">
					<text class="label">预约到访</text>
					<dateTime @getDate="_getDate"></dateTime>
				</view>
				<remarkBox :textName="'备注'" @getValue="_getValue"></remarkBox>
			</view>
			<!-- 更多信息 -->
			<view class="info_box">
				<view class="moreInfo" @click="switchInfo">
					<view class="infoName">完善信息<text>(非必填)</text></view>
					<uni-icons :type="showIcon"></uni-icons>
				</view>
				<view class="isSowInfo" v-if="showInfo">
					<view class="info_item">
						<text class="label">婚姻状况</text>
						<view class="flexBet">
							<ld-select :underlist="marryList" list-key="label" value-key="value" placeholder="请选择婚姻状况"
								v-model="form.marryState" @change="marryChange"></ld-select>
							<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
						</view>
					</view>
					<view class="info_item">
						<text class="label">意向级别</text>
						<view class="flexBet">
							<ld-select :underlist="intenLeveList" list-key="label" value-key="value"
								placeholder="请选择意向级别" v-model="form.intensionLevel" @change="selectIntenLeve">
							</ld-select>
							<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
						</view>
					</view>
					<view class="info_item">
						<text class="label">添加分组</text>
						<view class="flexBet">
							<ld-select :underlist="groupList" list-key="label" value-key="value" placeholder="请选择分组"
								v-model="groupIds" :multiple="true" @change="selectGroup"></ld-select>
							<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
						</view>
					</view>
					<remarkBox :textName="'跟进内容'" @getValue="_getfollowValue"></remarkBox>
					<view class="info_item">
						<text class="label">下次跟进</text>
						<dateTime @getDate="_getfollowDate"></dateTime>
					</view>
					<view class="info_item">
						<text class="label">认知途径</text>
						<view class="flexBet">
							<ld-select :underlist="cognitiveList" list-key="label" value-key="value"
								placeholder="请选择认知途径" v-model="form.cognitiveApproach" @change="selectCognitive">
							</ld-select>
							<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="footer" @click="registerTap">立即登记</view>
	</view>
</template>

<script>
	import dateTime from '@/components/dateTime.vue';
	import remarkBox from '@/components/remark.vue';
	import ldSelect from '@/components/ld-select/ld-select.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				showInfo: false, // 是否展示完善信息
				showIcon: 'arrowdown',
				form: {
					custName: '', // 客户姓名
					custPhone: '', // 手机号码
					gender: 0, // 性别
					custSource: 0, // 客户来源
					reportTime: '', // 预约时间
					remark: '', // 备注
					marryState: '', // 婚姻状况
					intensionLevel: '', // 意向级别
					groupId: [], // 分组id
					followUpContent: '', // 跟进内容
					nextFollowTime: '', // 下次跟进
					cognitiveApproach: '', // 认知途径
				},
				sexList: ['男', '女', '保密'], // 性别list
				customList: ['自来', '销售邀约', '渠道'], // 客户来源
				// 完善信息
				groupList: [], // 分组列表
				intenLeveList: [], // 意向级别列表
				cognitiveList: [], // 认知途径列表
				groupIds: [], // 分组id
				marryList: [{
					label: '未婚',
					value: '0'
				}, {
					label: '已婚',
					value: '1'
				}, {
					label: '保密',
					value: '2'
				}]
			}
		},
		onLoad() {
			this._getList()
		},
		onReady() {
			// this.$refs.uForm.setRules(this.rules);
		},
		methods: {
			// 展示收起完善信息
			switchInfo() {
				this.showInfo = !this.showInfo
				this.showIcon = this.showInfo ? 'arrowup' : 'arrowdown'
			},
			// 获取分组、意向级别、认知途径
			_getList() {
				Tools.showLoading('加载中')
				let that = this
				httpUtils.sendHttpRequest('2021', Object.assign({}, ''), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.groupList = res.data.data.groups
						that.intenLeveList = res.data.data.intentionLevels
						that.cognitiveList = res.data.data.cognitiveApproachs
					}
				})
			},
			// 性别选择
			sexChange: function(e) {
				this.form.gender = e.target.value
			},
			// 客户来源
			customChange: function(e) {
				this.form.custSource = e.target.value
			},
			// 日期回调
			_getDate(data) {
				this.form.reportTime = data
			},
			// 备注回调
			_getValue(data) {
				this.form.remark = data
			},
			// 完善信息选择
			// 下次跟进日期回调
			_getfollowDate(data) {
				this.form.nextFollowTime = data
			},
			// 跟进内容回调
			_getfollowValue(data) {
				this.form.followUpContent = data
			},
			// 婚姻状况选择
			marryChange(val) {
				this.form.marryState = val
			},
			// 意向级别选择
			selectIntenLeve(val) {
				this.form.intensionLevel = val
			},
			// 分组选择
			selectGroup(val) {
				this.groupIds = val
				this.form.groupId = this.groupIds
			},
			// 认知途径选择
			selectCognitive(val) {
				this.form.cognitiveApproach = val
			},
			// 立即登记
			registerTap() {
				if (!this.form.custName) {
					Tools.showErrorToast('请输入客户姓名')
					return
				}
				if (!this.form.custPhone || !(/^[1][3,4,5,6,7,8,9][0-9]{9}$/.test(this.form.custPhone))) {
					Tools.showErrorToast(!this.form.custPhone ? '请填写手机号' : '手机格式不正确')
					return
				}
				if (this.form.custSource == 1 && !this.form.reportTime) {
					Tools.showErrorToast('请选择预约到访时间')
					return
				}
				Tools.showLoading('提交中')
				httpUtils.sendHttpRequest('2020', Object.assign({}, this.form), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						Tools.showSuccessToast('登记成功')
						setTimeout(function() {
							const pages = getCurrentPages();
							if (pages.length > 1) {
								let beforePage = pages[pages.length - 2]; 
								beforePage.$vm._refreshList();
							 }
							 uni.navigateBack({
							 	delta : 1
							 })
						}, 500)
					}
				})
			}
		},
		components: {
			dateTime,
			remarkBox
		}
	}
</script>

<style lang="scss">
	@import '@/styles/form.scss';

	page {
		.requiredLabel {
			.u-form-item--left {
				:after {
					content: '*';
					color: red;
					padding-left: 10rpx;
				}
			}
		}
	}
</style>
